@import '../dimensions';

.measure-cell {
  white-space: nowrap;
  padding-left: $measure-column-padding-left;

  overflow: hidden;
  padding-top: 8px;

  display: inline-block;
  width: $measure-width;
  height: calc(100% + 2px);
  margin-top: -1px;

  &.all-alone {
    width: 100%;
  }

  .measure-label {
    padding-left: 4px;
  }
}
