@import '../../../imports';
@import '../../../components/tabular-scroller/dimensions';

.highlight-cont {
  position: absolute;
  left: $space-left;
  right: 0;
  top: $header-height;
  bottom: 0;
  overflow: hidden;

  .highlighter {
    @include border-css-variable(border, 1px, solid, 'hover');
    right: 0;
    height: $row-height + 1;
    margin-top: -1px;

    &::after {
      @include unpin-full(-1px);
      @include border-css-variable(border, 1px, dashed, highlight-border);
      content: '';
    }
  }
}
