@import '../../imports';

.highlight-modal {
  max-width: 400px;

  .title {
    color: #999;
    line-height: 17px;
    display: block;
    padding-bottom: 9px;
    overflow: hidden;
    word-wrap: break-word;
  }

  .value {
    white-space: nowrap;
  }

  .actions {
    margin-top: 7px;
    white-space: nowrap;

    .accept {
      margin-right: 5px;
    }
  }
}
